import { IStats } from "../util/createStats";
import { Dispose } from "../core/Dispose";
import { SRenderbufferColor } from "../core/Support";
import { CRenderbufferColor } from "../core/Constant";

/**
 * RBO 
 */
const RENDERBUFFER_SET: Map<number, GRenderbuffer> = new Map();

/**
 * @author axmand
 * @description
 * 1. renderbuffer2d
 * -
 * -FBO
 * 
 * 2.pbufferwebgl
 * -RBOtextureattachfbo
 * -ColorDepthStencil
 * -FBO
 * 
 * 3. gl.clear(gl.COLOR_BUFFER_BIT|gl.DEPTH_BUFFER_BIT|gl.STENCIL_BUFFER_BIT)FBO
 * 
 */
class GRenderbuffer extends Dispose {
    /**
     * 
     */
    dispose(): void {
        const gl = this.gl;
        const handler = this.renderbuffer;
        gl.bindRenderbuffer(gl.RENDERBUFFER, null);
        gl.deleteRenderbuffer(handler);
        this.renderbuffer = null;
        this.refCount = 0;
        RENDERBUFFER_SET.delete(this.ID);
        this.stats.renderbufferCount--;
    }

    /**
     * 
     */
    decRef(): void {
        if (--this.refCount <= 0) this.dispose();
    }

    /**
     * 
     */
    private gl: WebGLRenderingContext;

    /**
     * actual webgl rbo
     */
    private renderbuffer: WebGLRenderbuffer;

    /**
     * RBOgl.RGBA4
     */
    private format: SRenderbufferColor;

    /**
     * RBO - pixel width
     */
    private width: number;

    /**
     * RBO - pixel height
     */
    private height: number;

    /**
     * global stats
     */
    private stats: IStats;

    /**
     * RBO - pixel width
     */
    get Width(): number {
        return this.width;
    }

    /**
     * RBO - pixel height
     */
    get Height(): number {
        return this.height;
    }

    /**
     * actual webgl rbo
     */
    get Renderbuffer(): WebGLRenderbuffer {
        return this.renderbuffer;
    }

    /**
     * 
     */
    get Format(): number {
        return CRenderbufferColor[this.format];
    }

    /**
     * 
     * @param gl 
     * @param width 
     * @param height 
     * @param format 
     * @param stats 
     */
    constructor(
        gl: WebGLRenderingContext,
        width: number,
        height: number,
        format: SRenderbufferColor,
        stats: IStats,
    ) {
        super();
        this.gl = gl;
        this.renderbuffer = gl.createRenderbuffer();
        this.format = format || 'RGBA4';
        this.width = width;
        this.height = height;
        this.stats = stats;
        this.stats.renderbufferCount++;
        RENDERBUFFER_SET.set(this.ID, this);
    }

    /**
     * 
     */
    public bind = (): void => {
        const gl = this.gl;
        gl.bindRenderbuffer(gl.RENDERBUFFER, this.renderbuffer);
        gl.renderbufferStorage(gl.RENDERBUFFER, this.Format, this.width, this.height);
        this.refCount++;
    }
}

export {
    RENDERBUFFER_SET,
    GRenderbuffer
}