import { SColorSpace, STextureColor, STextureComponent, STextureCompressed } from "../core/Support";

/**
 * 
 * -texture
 */
interface ITexFlag {
    /**
     * 
     * gl.RGBA
     */
    texColor?: STextureColor | STextureCompressed;

    /**
     * 
     * gl.RGBA
     */
    inTexColor?: STextureColor | STextureCompressed;

    /**
     * 
     * gl.BYTE
     */
    component?: STextureComponent;

    /**
     * 
     * 
     */
    compressed?: boolean;

    /**
     * alphaglalpha
     * blendingRGBalpha
     * 1
     * 2
     * https://segmentfault.com/a/1190000002990030
     * false
     */
    premultiplyAlpha?: boolean;

    /**
     * Y,Y
     * false
     * 0-------- 1.0               1.0
     * |                           |
     * |                           |
     * |                           |-------- 1.0
     * 1.0                         0
     *              
     */
    flipY?: boolean;

    /**
     * gl.DrawPixel
     * drawPixelbyte
     */
    unpackAlignment?: 1 | 2 | 4 | 8;

    /**
     * GL_BROWSER_DEFAULT_WEBGL
     * WebGL
     * gl.NONE
     */
    colorSpace?: SColorSpace;

    /**
     * shape - width
     */
    width?: number;

    /**
     * shape -height
     */
    height?: number;

    /**
     * shape - channel 
     * <=4 (RGBA)
     */
    channels?: number;
}

/**
 * texture
 */
const createTexFlag = (): ITexFlag => {
    const texFlags: ITexFlag = {
        inTexColor: 'RGBA',
        texColor: 'RGBA',
        component: 'UNSIGNED_BYTE',
        compressed: false,
        premultiplyAlpha: false,                //alpha
        flipY: false,
        unpackAlignment: 1,                     //
        colorSpace: 'BROWSER_DEFAULT_WEBGL',    //0x9244, 
        width: 0,                               //0
        height: 0,                              //0
        channels: 0                             //0
    };
    return texFlags;
}

export { 
    ITexFlag,
    createTexFlag 
}