import { getMutableClone } from "typescript";
import { QuadtreeTile, QuadtreeTileSchema } from "../..";
import { llpVertex } from "../../util/llp";
import { rgba } from "../../util/rgba";
import { Sketchpad, TSketchpadDataSchema } from "../Sektchpad";

interface ITileLayerDataSchema extends TSketchpadDataSchema {
    key: string;
    uri?: string;
    width?: number;
    height?: number;
    channel?: number;
    vertices?: number[][];
    textureBuffer?: Uint8Array;
}

/**
 * 
 */
class TileLayer extends Sketchpad<ITileLayerDataSchema>{
    /**
     * 
     */
    protected CACHE: Map<string, { uri: string, vertices: number[][] }> = new Map();

    /**
     * 
     */
    registerEvents = () => {
        this.g.on('tileupdated', this.prepareData, this);
        this.g.on('worker', this.prepareWorker, this);
    }

    /**
     * 
     */
    protected queue: ITileLayerDataSchema[] = [];

    /**
     * 
     * @param tiles 
     */
    private prepareData = (tiles: QuadtreeTile[]) => {
        this.queue = [];
        const CACHE = this.CACHE;
        //
        tiles?.forEach((t: QuadtreeTile) => {
            const { X: x, Y: y, Level: level, Boundary: boundary } = t;
            const key = `${level}-${x}-${y}`;
            if (!CACHE.has(key)) {
                // const uri = `https://mt0.google.cn/maps/vt?lyrs=y&hl=zh-CN&gl=CN&&x=${x}&y=${y}&z=${level}&scale=2`;
                const uri = `https://server.arcgisonline.com/arcgis/rest/services/NatGeo_World_Map/MapServer/tile/${level}/${y}/${x}`;
                const vertices = llpVertex(boundary);
                const q: ITileLayerDataSchema = {
                    key,
                    uri,
                    vertices
                };
                this.queue.unshift(q);
            }
        });
    }

    /**
     * 
     */
    private prepareWorker = () => {
        const CACHE = this.CACHE, QUEUE = this.queue;
        if (QUEUE.length > 0 && !CACHE.has(QUEUE[0].key)) {
            const q = QUEUE.shift();
            const { uri, vertices, key } = q;
            CACHE.set(key, { uri, vertices });
            rgba(uri, key).then(v => {
                const schema: ITileLayerDataSchema = {
                    key: v.key,
                    width: v.w,
                    height: v.h,
                    channel: v.c,
                    vertices: vertices,
                    textureBuffer: v.buf
                };
                this.Renderer.prepare(schema);
            });
        }
    }
}

export {
    ITileLayerDataSchema,
    TileLayer,
}