

/**
 * reference https://github.com/toji/gl-matrix/blob/master/src/gl-matrix/vec4.js
 * switch to es6 syntax
 * warning:if you don't want to change the source value,please use vec4.clone().* instead of vec4.*
 * @author yellow 2017/5/9
 */
import { Mat4 } from "./Mat4";
import { GLMatrix } from './GLMatrix';
import { Quat } from "./Quat";
/**
 * @class 4 Dimensional Vector
 * @name vec4
 */
export class Vec4 {
    /**
     * vec4 array store
     */
    _out: number[];
    /**
     *  Creates a new, empty vec4
     */
    constructor() {
        this._out = new Array(4);
        this._out[0] = 0;
        this._out[1] = 0;
        this._out[2] = 0;
        this._out[3] = 0;
    }
    /**
     * 
     */
    get x() {
        return this._out[0];
    }
    /**
     * 
     */
    get y() {
        return this._out[1];
    }
    /**
     * 
     */
    get z() {
        return this._out[2];
    }
    /**
     * 
     */
    get w() {
        return this._out[3];
    }
    /**
     * adapter for webgl matrix
     * get the array directly
     * @memberof vec4
     * @return {Array}
     */
    get value(): number[] {
        return this._out;
    }
    /**
     * Generates a random vector with the given scale
     * @param {number} [s] Length of the resulting vector. If ommitted, a unit vector will be returned
     */
    static random(s: number = 1.0): Vec4 {
        s = s || 1.0;
        const vec = new Vec4();
        //TODO: This is a pretty awful way of doing this. Find something better.
        vec.set(GLMatrix.RANDOM(), GLMatrix.RANDOM(), GLMatrix.RANDOM(), GLMatrix.RANDOM()).normalize().scale(s);
        return vec;
    }
    /**
     * set the value of vec4
     */
    set(x: number, y: number, z: number, w: number): Vec4 {
        this._out[0] = x;
        this._out[1] = y;
        this._out[2] = z;
        this._out[3] = w;
        return this;
    }
    /**
     * Creates a new vec4 initialized with values from an existing vector
     */
    clone(): Vec4 {
        const vec = new Vec4();
        vec.set(this._out[0], this._out[1], this._out[2], this._out[3]);
        return vec;
    }
    /**
     * Adds two vec4's
     * @param {Vec4} vec
     */
    add(vec: Vec4): Vec4 {
        this._out[0] += vec.value[0];
        this._out[1] += vec.value[1];
        this._out[2] += vec.value[2];
        this._out[3] += vec.value[3];
        return this;
    }
    /**
     * Subtracts vector vec from vector this
     */
    sub(vec: Vec4): Vec4 {
        this._out[0] -= vec.value[0];
        this._out[1] -= vec.value[1];
        this._out[2] -= vec.value[2];
        this._out[3] -= vec.value[3];
        return this;
    }
    /**
     * Multiplies two vec4's
     */
    multiply(vec: Vec4): Vec4 {
        this._out[0] *= vec.value[0];
        this._out[1] *= vec.value[1];
        this._out[2] *= vec.value[2];
        this._out[3] *= vec.value[3];
        return this;
    }
    /**
    * Divides two vec4's
    */
    divide(vec: Vec4): Vec4 {
        this._out[0] /= vec.value[0];
        this._out[1] /= vec.value[1];
        this._out[2] /= vec.value[2];
        this._out[3] /= vec.value[3];
        return this;
    }
    /**
     * Math.ceil the components of a vec4
     */
    ceil(): Vec4 {
        this._out[0] = Math.ceil(this._out[0]);
        this._out[1] = Math.ceil(this._out[1]);
        this._out[2] = Math.ceil(this._out[2]);
        this._out[3] = Math.ceil(this._out[3]);
        return this;
    }
    /**
     * Math.round the components of a vec4
     */
    round(): Vec4 {
        this._out[0] = Math.round(this._out[0]);
        this._out[1] = Math.round(this._out[1]);
        this._out[2] = Math.round(this._out[2]);
        this._out[3] = Math.round(this._out[3]);
        return this;
    }
    /**
    * Math.floor the components of a vec4
    */
    floor(): Vec4 {
        this._out[0] = Math.floor(this._out[0]);
        this._out[1] = Math.floor(this._out[1]);
        this._out[2] = Math.floor(this._out[2]);
        this._out[3] = Math.floor(this._out[3]);
        return this;
    }
    /**
     * Returns the minimum of two vec4's
     * @param {Vec4} vec
     */
    min(vec: Vec4): Vec4 {
        this._out[0] = Math.min(this._out[0], vec.value[0]);
        this._out[1] = Math.min(this._out[1], vec.value[1]);
        this._out[2] = Math.min(this._out[2], vec.value[2]);
        this._out[3] = Math.min(this._out[3], vec.value[3]);
        return this;
    }
    /**
     * Returns the maximum of two vec4's
     * @param {Vec4} vec
     */
    max(vec: Vec4): Vec4 {
        this._out[0] = Math.max(this._out[0], vec.value[0]);
        this._out[1] = Math.max(this._out[1], vec.value[1]);
        this._out[2] = Math.max(this._out[2], vec.value[2]);
        this._out[3] = Math.max(this._out[3], vec._out[3]);
        return this;
    }
    /**
     * Scales a vec4 by a scalar number
     * @param {number} s the scale
     */
    scale(s: number): Vec4 {
        this._out[0] *= s;
        this._out[1] *= s;
        this._out[2] *= s;
        this._out[3] *= s;
        return this;
    }
    /**
     * Calculates the euclidian distance between two vec4's
     * @param {Vec4} vec the distance to vec
     */
    distance(vec: Vec4): number {
        let [x0, y0, z0, w0] = this._out,
            [x1, y1, z1, w1] = vec._out,
            x = x0 - x1,
            y = y0 - y1,
            z = z0 - z1,
            w = w0 - w1;
        return Math.sqrt(x * x + y * y + z * z + w * w);
    }
    /**
     * Calculates the length of a vec4
     */
    len() {
        return this.distance(new Vec4());
    }
    /**
     * Negates the components of a vec4
     */
    negate() {
        this._out[0] = -this._out[0];
        this._out[1] = -this._out[1];
        this._out[2] = -this._out[2];
        this._out[3] = -this._out[3];
        return this;
    }
    /**
     * Returns the inverse of the components of a vec4
     */
    inverse() {
        this._out[0] = 1.0 / this._out[0];
        this._out[1] = 1.0 / this._out[1];
        this._out[2] = 1.0 / this._out[2];
        this._out[3] = 1.0 / this._out[3];
    }
    /**
     * Normalize a vec4
     */
    normalize() {
        let len = this.len();
        if (len > 0) {
            this._out[0] /= len;
            this._out[1] /= len;
            this._out[2] /= len;
            this._out[3] /= len;
        }
        return this;
    }
    /**
     * @param {Vec4} vec
     */
    dot(vec: Vec4): number {
        const [x0, y0, z0, w0] = this.value,
            [x1, y1, z1, w1] = vec.value;
        return x0 * x1 + y0 * y1 + z0 * z1 + w0 * w1;
    }
    /**
     * 
     * @param {Vec4} v1 the first operand
     * @param {Vec4} v2 the second operand
     * @param {Number} t interpolation amount, in the range [0-1], between the two inputs
     */
    lerp(v1: Vec4, v2: Vec4, t: number): Vec4 {
        const [ax, ay, az, aw] = v1.value,
            [bx, by, bz, bw] = v2.value;
        this._out[0] = ax + t * (bx - ax);
        this._out[1] = ay + t * (by - ay);
        this._out[2] = az + t * (bz - az);
        this._out[3] = aw + t * (bw - aw);
        return this;
    }
    /**
     * Transforms the vec4 with a mat4.
     * @param {mat4} mat matrix to transform with
     */
    transformMat4(mat: Mat4): Vec4 {
        let [x, y, z, w] = this._out;
        this._out[0] = mat.value[0] * x + mat.value[4] * y + mat.value[8] * z + mat.value[12] * w;
        this._out[1] = mat.value[1] * x + mat.value[5] * y + mat.value[9] * z + mat.value[13] * w;
        this._out[2] = mat.value[2] * x + mat.value[6] * y + mat.value[10] * z + mat.value[14] * w;
        this._out[3] = mat.value[3] * x + mat.value[7] * y + mat.value[11] * z + mat.value[15] * w;
        return this;
    }
    /**
     * Transforms the vec4 with a quat
     * @param {quat} q quaternion to transform with
     */
    transformQuat(q: Quat): Vec4 {
        let [x, y, z, w] = this._out,
            [qx, qy, qz, qw] = q.value,
            ix = qw * x + qy * z - qz * y,
            iy = qw * y + qz * x - qx * z,
            iz = qw * z + qx * y - qy * x,
            iw = -qx * x - qy * y - qz * z;
        // calculate result * inverse quat
        this._out[0] = ix * qw + iw * -qx + iy * -qz - iz * -qy;
        this._out[1] = iy * qw + iw * -qy + iz * -qx - ix * -qz;
        this._out[2] = iz * qw + iw * -qz + ix * -qy - iy * -qx;
        this._out[3] = q.value[3];
        return this;
    }
    /**
     * Returns a string representation of a vector
     */
    toString(): string {
        return 'vec4(' + this._out[0] + ', ' + this._out[1] + ', ' + this._out[2] + ', ' + this._out[3] + ')';
    }
    /**
     * Returns whether or not the vectors have approximately the same elements in the same position.
     * @param {Vec4} vec
     */
    equals(vec: Vec4): boolean {
        const [a0, a1, a2, a3] = this._out,
            [b0, b1, b2, b3] = vec._out;
        return (Math.abs(a0 - b0) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a0), Math.abs(b0)) &&
            Math.abs(a1 - b1) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a1), Math.abs(b1)) &&
            Math.abs(a2 - b2) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a2), Math.abs(b2)) &&
            Math.abs(a3 - b3) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a3), Math.abs(b3)));
    }
}
