/**
 * reference https://github.com/toji/gl-matrix/blob/master/src/gl-matrix/vec2.js
 * switch to es6 syntax
 * warning:if you don't want to change the source value,please use vec2.clone().* instead of vec2.*
 * @author yellow 2017/5/8
 */

import { GLMatrix } from "./GLMatrix";
import { Mat3 } from "./Mat3";
import { Mat4 } from "./Mat4";

/**
 * @class
 */
export class Vec2 {
    /**
     * array store for vec2
     */
    private _out: number[];
    /**
     * Creates a new, empty vec2
     */
    constructor() {
        this._out = new Array(2);
        this._out[0] = 0;
        this._out[1] = 0;
    };
    /**
     * 
     */
    get x() {
        return this._out[0];
    }
    /**
     * 
     */
    get y() {
        return this._out[1];
    }
    /**
     * adapter for webgl matrix
     * get the array directly
     * @memberof vec2
     * @return {Array}
     */
    get value() {
        return this._out;
    };
    /**
     * generate a random vector
     */
    static random(scale: number = 1.0): Vec2 {
        scale = scale || 1.0;
        const r = GLMatrix.RANDOM() * 2.0 * Math.PI;
        const v = new Vec2().set(Math.cos(r) * scale, Math.sin(r) * scale);
        return v;
    };
    /**
     * set value of vec2,such as [x,y]
     */
    set(x: number, y: number): Vec2 {
        this._out[0] = x;
        this._out[1] = y;
        return this;
    };
    /**
     * Creates a new vec2 initialized with values from an existing vector
     */
    clone() {
        let vec = new Vec2();
        vec.set(this._out[0], this._out[1]);
        return vec;
    };
    /**
     * Add two vec2's
     * @param {Vec2} vec the vec2 which waiting for add
     */
    add(vec: Vec2): Vec2 {
        this._out[0] += vec._out[0];
        this._out[1] += vec._out[1];
        return this;
    };
    /**
     * substract vector vec from this
     * @param {Vec2} vec
     */
    sub(vec: Vec2): Vec2 {
        this._out[0] -= vec._out[0];
        this._out[1] -= vec._out[1];
        return this;
    };
    /**
     * multiplies two vec2's
     * @param {Vec2} 
     */
    multiply(vec: Vec2): Vec2 {
        this._out[0] *= vec._out[0];
        this._out[1] *= vec._out[1];
        return this;
    };
    /**
     * diveides two vec2's
     * 
     */
    divide(vec: Vec2): Vec2 {
        this._out[0] /= vec._out[0];
        this._out[1] /= vec._out[1];
        return this;
    };
    /**
     * use math.ceil to adjust the value of v0 v1
     * 
     */
    ceil(): Vec2 {
        this._out[0] = Math.ceil(this._out[0]);
        this._out[1] = Math.ceil(this._out[1]);
        return this;
    };
    /**
     * use math.floor to adjust the value of v0 v1
     */
    floor(): Vec2 {
        this._out[0] = Math.floor(this._out[0]);
        this._out[1] = Math.floor(this._out[1]);
        return this;
    };
    /**
     * use math.round to adjust the value of v0 v1
     */
    round(): Vec2 {
        this._out[0] = Math.round(this._out[0]);
        this._out[1] = Math.round(this._out[1]);
        return this;
    };
    /**
     * merge two vector's min value
     * 
     */
    min(vec: Vec2): Vec2 {
        this._out[0] = Math.min(this._out[0], vec._out[0]);
        this._out[1] = Math.min(this._out[1], vec._out[1]);
        return this;
    };
    /**
     *  merge two vector's max value
     */
    max(vec: Vec2): Vec2 {
        this._out[0] = Math.max(this._out[0], vec._out[0]);
        this._out[1] = Math.max(this._out[1], vec._out[1]);
        return this;
    };
    /**
     * Scales a vec2 by a scalar number
     * @param {Number} n
     */
    scale(n: number): Vec2 {
        this._out[0] *= n;
        this._out[1] *= n;
        return this;
    };
    /**
     * Calculates the euclidian distance between two vec2's
     */
    distance(v: Vec2): number {
        const x = this._out[0] - v._out[0],
            y = this._out[1] - v._out[1];
        return Math.sqrt(x * x + y * y);
    };
    /**
     * Calculates the manhattan distance between two vec2's
     */
    manhattanDistance(v: Vec2): number {
        const x = Math.abs(this._out[0] - v._out[0]),
            y = Math.abs(this._out[1] - v._out[1]);
        return x + y;
    };
    /**
     * Calculates the chebyshev distance between two vec2's
     */
    chebyshevDistance(v: Vec2): number {
        const x = Math.abs(this._out[0] - v._out[0]),
            y = Math.abs(this._out[1] - v._out[1]);
        return Math.max(x, y);
    };
    /**
     * Calculates the length of a vec2
     */
    len(): number {
        return this.distance(new Vec2());
    };
    /**
     * Negates the components of a vec2
     */
    negate(): Vec2 {
        this._out[0] = -this._out[0];
        this._out[1] = -this._out[1];
        return this;
    };
    /**
     * Returns the inverse of the components of a vec2
     */
    inverse(): Vec2 {
        this._out[0] = 1.0 / this._out[0];
        this._out[1] = 1.0 / this._out[1];
        return this;
    };
    /**
     * Normalize a vec2
     */
    normalize(): Vec2 {
        const len = this.len();
        if (len > 0) {
            //for the reason * has a high performance than /
            //}{fixed 2019.1.15 high precision
            this._out[0] /= len;
            this._out[1] /= len;
        }
        return this;
    };
    /**
     * Calculates the dot product of two vec2's
     */
    dot(v: Vec2): number {
        return this._out[0] * v._out[0] + this._out[1] * v._out[1];
    };
    /**
     * performs a linear interpolation between two vec2's
     * @param {Vec2} v
     * @param {number} t interpolation amount between the two inputs
     */
    lerp(v: Vec2, t: number): Vec2 {
        const [ax, ay] = this._out,
            [bx, by] = v._out;
        this._out[0] = ax + t * (bx - ax);
        this._out[1] = ay + t * (by - ay);
        return this;
    };
    /**
     * Returns a string representation of a vector
     */
    toString(): string {
        return 'vec2(' + this._out[0] + ',' + this._out[1] + ')';
    };
    /**
     * Transforms the vec2 with a mat3
     * @param {mat3} mat matrix to transform with
     */
    transformMat3(mat: Mat3): Vec2 {
        const [x, y] = this._out;
        this._out[0] = mat._out[0] * x + mat._out[3] * y + mat._out[6];
        this._out[1] = mat._out[1] * x + mat._out[4] * y + mat._out[7];
        return this;
    };
    /**
     * Transforms the vec2 with a mat4
     */
    transformMat4(mat: Mat4): Vec2 {
        const [x, y] = this._out;
        this._out[0] = mat.value[0] * x + mat.value[4] * y + mat.value[5];
        this._out[1] = mat.value[1] * x + mat.value[5] * y + mat.value[13];
        return this;
    };
    /**
     * Returns whether or not the vectors have approximately the same elements in the same position.
     * precision
     */
    equals(vec: Vec2): boolean {
        const [a0, a1] = this._out,
            [b0, b1] = vec._out;
        return (Math.abs(a0 - b0) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a0), Math.abs(b0)) &&
            Math.abs(a1 - b1) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a1), Math.abs(b1)));
    };
};