/**
 * reference https://github.com/toji/gl-matrix/blob/master/src/gl-matrix/vec3.js
 * switch to es6 syntax
 * warning:if you don't want to change the source value,please use vec3.clone().* instead of vec3.*
 * @author yellow 2017/5/8
 * 
 */

 import { GLMatrix } from "./GLMatrix";
import { Mat3 } from "./Mat3";
import { Mat4 } from "./Mat4";
import { Quat } from "./Quat";

/**
 * @class 3 Dimensional Vector
 */
export class Vec3 {
    /**
     * array store for vec3
     */
    private _out: number[];
    /**
     * Creates a new, empty vec3
     */
    constructor() {
        this._out = new Array(3);
        this._out[0] = 0;
        this._out[1] = 0;
        this._out[2] = 0;
    }
    /**
     * 
     */
    get x() {
        return this._out[0];
    }
    /**
     * 
     */
    get y() {
        return this._out[1];
    }
    /**
     * 
     */
    get z() {
        return this._out[2];
    }
    /**
     * adapter for webgl matrix
     * get the array directly
     * @memberof vec3
     * @return {Array}
     */
    get value() {
        return this._out;
    }
    /**
     * set value of v0 v1 v2
     */
    set(x: number, y: number, z: number) {
        this._out[0] = x;
        this._out[1] = y;
        this._out[2] = z;
        return this;
    }
    /**
     * Creates a new vec3 initialized with values from an existing vector
     */
    clone(): Vec3 {
        let vec = new Vec3();
        vec.set(this._out[0], this._out[1], this._out[2]);
        return vec;
    }
    /**
     * Adds two vec3's
     * @param {vec3} vec 
     */
    add(vec: Vec3): Vec3 {
        this._out[0] += vec._out[0];
        this._out[1] += vec._out[1];
        this._out[2] += vec._out[2];
        return this;
    }
    /**
     * Subtracts vector vec from vector this
     * @param {vec3} vec
     */
    sub(vec: Vec3): Vec3 {
        this._out[0] -= vec._out[0];
        this._out[1] -= vec._out[1];
        this._out[2] -= vec._out[2];
        return this;
    }
    /**
     * Multiplies two vec3's
     */
    multiply(vec: Vec3): Vec3 {
        this._out[0] *= vec._out[0];
        this._out[1] *= vec._out[1];
        this._out[2] *= vec._out[2];
        return this;
    }
    /**
     * Divides two vec3's
     * @param vec 
     */
    divide(vec: Vec3): Vec3 {
        this._out[0] /= vec._out[0];
        this._out[1] /= vec._out[1];
        this._out[2] /= vec._out[2];
        return this;
    }
    /**
     * Math.ceil the components of a vec3
     */
    ceil(): Vec3 {
        this._out[0] = Math.ceil(this._out[0]);
        this._out[1] = Math.ceil(this._out[1]);
        this._out[2] = Math.ceil(this._out[2]);
        return this;
    }
    /**
     * Math.floor the components of a vec3
     */
    floor(): Vec3 {
        this._out[0] = Math.floor(this._out[0]);
        this._out[1] = Math.floor(this._out[1]);
        this._out[2] = Math.floor(this._out[2]);
        return this;
    }
    /**
     * Math.round the components of a vec3
     */
    round() {
        this._out[0] = Math.round(this._out[0]);
        this._out[1] = Math.round(this._out[1]);
        this._out[2] = Math.round(this._out[2]);
        return this;
    }
    /**
     * Returns the minimum of two vec3's
     */
    min(vec: Vec3): Vec3 {
        this._out[0] = Math.min(this._out[0], vec._out[0]);
        this._out[1] = Math.min(this._out[1], vec._out[1]);
        this._out[2] = Math.min(this._out[2], vec._out[2]);
        return this;
    }
    /**
     * Returns the maximum of two vec3's
     */
    max(vec: Vec3): Vec3 {
        this._out[0] = Math.max(this._out[0], vec._out[0]);
        this._out[1] = Math.max(this._out[1], vec._out[1]);
        this._out[2] = Math.max(this._out[2], vec._out[2]);
        return this;
    }
    /**
     * Scales a vec3 by a scalar number
     * @param {number} n amount to scale the vector by
     */
    scale(n: number): Vec3 {
        this._out[0] *= n;
        this._out[1] *= n;
        this._out[2] *= n;
        return this;
    }
    /**
     * Calculates the euclidian distance between two vec3's
     * @param {vec3} vec
     */
    distance(vec: Vec3): number {
        const [x0, y0, z0] = this._out,
            [x1, y1, z1] = vec._out,
            x = x0 - x1,
            y = y0 - y1,
            z = z0 - z1;
        return Math.sqrt(x * x + y * y + z * z);
    }
    /**
     * Calculates the length of a vec3
     */
    len() {
        return this.distance(new Vec3());
    }
    /**
     * Negates the components of a vec3
     */
    negate() {
        this._out[0] = -this._out[0];
        this._out[1] = -this._out[1];
        this._out[2] = -this._out[2];
        return this;
    }
    /**
     * Returns the inverse of the components of a vec3
     */
    inverse() {
        this._out[0] = 1.0 / this._out[0];
        this._out[1] = 1.0 / this._out[1];
        this._out[2] = 1.0 / this._out[2];
        return this;
    }
    /**
     * Normalize a vec3
     */
    normalize() {
        let len = this.len();
        if (len > 0) {
            this._out[0] /= len;
            this._out[1] /= len;
            this._out[2] /= len;
        }
        return this;
    }
    /**
     * Calculates the dot product of two vec3's
     * @param {vec3} vec
     */
    dot(vec: Vec3): number {
        let [x0, y0, z0] = this._out,
            [x1, y1, z1] = vec._out;
        return x0 * x1 + y0 * y1 + z0 * z1;
    }
    /**
     * Computes the cross product of two vec3's
     * https://webgl2fundamentals.org/webgl/lessons/webgl-3d-camera.html
     * calcue the perpendicular vec3 
     * @param {Vec3} v3
     * @return {Vec3}
     * @example
     * let v3_out = v3_in1.clone().cross(v3_in2);
     * the v3_out perpendicular to v3_in1 and v3_in2
     */
    cross(v3: Vec3): Vec3 {
        let [ax, ay, az] = this._out,
            [bx, by, bz] = v3.value;
        this._out[0] = ay * bz - az * by;
        this._out[1] = az * bx - ax * bz;
        this._out[2] = ax * by - ay * bx;
        return this;
    }
    /**
     * Performs a linear interpolation between two vec3's
     * @param {Vec3} vec
     * @param {number} t
     */
    lerp(vec: Vec3, t: number): Vec3 {
        let [ax, ay, az] = this._out,
            [bx, by, bz] = vec._out;
        this._out[0] = ax + t * (bx - ax);
        this._out[1] = ay + t * (by - ay);
        this._out[2] = az + t * (bz - az);
        return this;
    }
    /**
     * Performs a hermite interpolation with two control points
     * @param {Vec3} v1
     * @param {Vec3} v1
     * @param {Vec3} v1
     * @param {number} t interpolation amount between the two inputs
     */
    hermite(v1: Vec3, v2: Vec3, v3: Vec3, t: number): Vec3 {
        const factorTimes2 = t * t,
            factor1 = factorTimes2 * (2 * t - 3) + 1,
            factor2 = factorTimes2 * (t - 2) + t,
            factor3 = factorTimes2 * (t - 1),
            factor4 = factorTimes2 * (3 - 2 * t);
        this._out[0] = this._out[0] * factor1 + v1._out[0] * factor2 + v2._out[0] * factor3 + v3._out[0] * factor4;
        this._out[1] = this._out[1] * factor1 + v1._out[1] * factor2 + v2._out[1] * factor3 + v3._out[1] * factor4;
        this._out[2] = this._out[2] * factor1 + v1._out[2] * factor2 + v2._out[2] * factor3 + v3._out[2] * factor4;
        return this;
    }
    /**
     * Performs a bezier interpolation with two control points
     * @param {Vec3} v1
     * @param {Vec3} v2
     * @param {Vec3} v3
     * @param {Number} t interpolation amount between the two inputs
     */
    bezier(v1: Vec3, v2: Vec3, v3: Vec3, t: number): Vec3 {
        const inverseFactor = 1 - t,
            inverseFactorTimesTwo = inverseFactor * inverseFactor,
            factorTimes2 = t * t,
            factor1 = inverseFactorTimesTwo * inverseFactor,
            factor2 = 3 * t * inverseFactorTimesTwo,
            factor3 = 3 * factorTimes2 * inverseFactor,
            factor4 = factorTimes2 * t;
        this._out[0] = this._out[0] * factor1 + v1._out[0] * factor2 + v2._out[0] * factor3 + v3._out[0] * factor4;
        this._out[1] = this._out[1] * factor1 + v1._out[1] * factor2 + v2._out[1] * factor3 + v3._out[1] * factor4;
        this._out[2] = this._out[2] * factor1 + v1._out[2] * factor2 + v2._out[2] * factor3 + v3._out[2] * factor4;
        return this
    }
    /**
     * Transforms the vec3 with a mat4.
     * 4th vector component is implicitly '1'
     * @param {mat4} mat the 4x4 matrix to transform with
     */
    transformMat4(mat: Mat4): Vec3 {
        const [x, y, z] = this._out,
            w = (mat.value[3] * x + mat.value[7] * y + mat.value[11] * z + mat.value[15]) || 1.0;
        this._out[0] = (mat.value[0] * x + mat.value[4] * y + mat.value[8] * z + mat.value[12]) / w;
        this._out[1] = (mat.value[1] * x + mat.value[5] * y + mat.value[9] * z + mat.value[13]) / w;
        this._out[2] = (mat.value[2] * x + mat.value[6] * y + mat.value[10] * z + mat.value[14]) / w;
        return this;
    }
    /**
     * Transforms the vec3 with a mat3.
     * @param {mat3} mat  the 3x3 matrix to transform with
     */
    transformMat3(mat: Mat3): Vec3 {
        const [x, y, z] = this._out;
        this._out[0] = x * mat._out[0] + y * mat._out[3] + z * mat._out[6];
        this._out[1] = x * mat._out[1] + y * mat._out[4] + z * mat._out[7];
        this._out[2] = x * mat._out[2] + y * mat._out[5] + z * mat._out[8];
        return this;
    }
    /**
     * returns a string represent vec3
     */
    toString(): string {
        return 'vec3(' + this._out[0] + ', ' + this._out[1] + ', ' + this._out[2] + ')';
    }
    /**
     * ransforms the vec3 with a quat
     * benchmarks: http://jsperf.com/quaternion-transform-vec3-implementations
     * @param {quat} q quaternion to transform with
     */
    transformQuat(q: Quat): Vec3 {
        let [x, y, z] = this._out,
            [qx, qy, qz, qw] = q.value,
            // calculate quat * vec
            ix = qw * x + qy * z - qz * y,
            iy = qw * y + qz * x - qx * z,
            iz = qw * z + qx * y - qy * x,
            iw = -qx * x - qy * y - qz * z;
        this._out[0] = ix * qw + iw * -qx + iy * -qz - iz * -qy;
        this._out[1] = iy * qw + iw * -qy + iz * -qx - ix * -qz;
        this._out[2] = iz * qw + iw * -qz + ix * -qy - iy * -qx;
        return this;
    }
    /**
     * Rotate a 3D vector around the x-axis
     * @param {Vec3} v the origin of the rotation
     * @param {number} c the angle of rotation
     */
    rotateX(v: Vec3, c: number): Vec3 {
        var p = [], r = [];
        //Translate point to the origin
        p[0] = this._out[0] - v._out[0];
        p[1] = this._out[1] - v._out[1];
        p[2] = this._out[2] - v._out[2];
        //perform rotation
        r[0] = p[0];
        r[1] = p[1] * Math.cos(c) - p[2] * Math.sin(c);
        r[2] = p[1] * Math.sin(c) + p[2] * Math.cos(c);
        //translate to correct position
        this._out[0] = r[0] + v.value[0];
        this._out[1] = r[1] + v.value[1];
        this._out[2] = r[2] + v.value[2];
        return this;
    }
    /**
     * Rotate a 3D vector around the y-axis
     * @param {Vec3} v The origin of the rotation
     * @param {number} c The angle of rotation
     */
    rotateY(v: Vec3, c: number): Vec3 {
        var p = [], r = [];
        //Translate point to the origin
        p[0] = this._out[0] - v._out[0];
        p[1] = this._out[1] - v._out[1];
        p[2] = this._out[2] - v._out[2];
        //perform rotation
        r[0] = p[2] * Math.sin(c) + p[0] * Math.cos(c);
        r[1] = p[1];
        r[2] = p[2] * Math.cos(c) - p[0] * Math.sin(c);
        //translate to correct position
        this._out[0] = r[0] + v.value[0];
        this._out[1] = r[1] + v.value[1];
        this._out[2] = r[2] + v.value[2];
        return this;
    }
    /**
     * Rotate a 3D vector around the z-axis
     * @param {Vec3} v The origin of the rotation
     * @param {number} c the angle of rotation
     */
    rotateZ(v: Vec3, c: number): Vec3 {
        var p = [], r = [];
        //Translate point to the origin
        p[0] = this._out[0] - v._out[0];
        p[1] = this._out[1] - v._out[1];
        p[2] = this._out[2] - v._out[2];
        //perform rotation
        r[0] = p[0] * Math.cos(c) - p[1] * Math.sin(c);
        r[1] = p[0] * Math.sin(c) + p[1] * Math.cos(c);
        r[2] = p[2];
        //translate to correct position
        this._out[0] = r[0] + v.value[0];
        this._out[1] = r[1] + v.value[1];
        this._out[2] = r[2] + v.value[2];
        return this;
    }
    /**
     * calcute the angle between two 3D vectors
     * @param {Vec3} v the second vector
     */
    angle(v: Vec3): number {
        const va = this.clone().normalize(),
            vb = v.clone().normalize();
        const cosine = va.clone().dot(vb);
        if (cosine > 1.0)
            return 0;
        else if (cosine < -1.0)
            return Math.PI;
        else
            return Math.acos(cosine);
    }
    /**
     * reference:
     * https://github.com/mrdoob/three.js/blob/dev/src/math/Vector3.js
     * @typedef {import("./Quat")} Quat 
     * @param {Quat} q
     */
    applyQuat(q: Quat): Vec3 {
        //
        const x = this.x, y = this.y, z = this.z,
            qx = q.x, qy = q.y, qz = q.z, qw = q.w;
        // calculate quat * vector
        const ix = qw * x + qy * z - qz * y,
            iy = qw * y + qz * x - qx * z,
            iz = qw * z + qx * y - qy * x,
            iw = - qx * x - qy * y - qz * z;
        // calculate result * inverse quat
        this._out[0] = ix * qw + iw * - qx + iy * - qz - iz * - qy;
        this._out[1] = iy * qw + iw * - qy + iz * - qx - ix * - qz;
        this._out[2] = iz * qw + iw * - qz + ix * - qy - iy * - qx;
        //
        return this;
    }
    /**
     * Returns whether or not the vectors have approximately the same elements in the same position.
     */
    equals(vec: Vec3): boolean {
        const [a0, a1, a2] = this._out,
            [b0, b1, b2] = vec._out;
        return (Math.abs(a0 - b0) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a0), Math.abs(b0)) &&
            Math.abs(a1 - b1) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a1), Math.abs(b1)) &&
            Math.abs(a2 - b2) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a2), Math.abs(b2)));
    }
    /**
     * 
     */
    applyMatrix4(m4: Mat4): Vec3 {
        const x = this.x, y = this.y, z = this.z;
        const w = 1 / (m4.value[3] * x + m4.value[7] * y + m4.value[11] * z + m4.value[15]);
        const x1 = (m4.value[0] * x + m4.value[4] * y + m4.value[8] * z + m4.value[12]) * w,
            y1 = (m4.value[1] * x + m4.value[5] * y + m4.value[9] * z + m4.value[13]) * w,
            z1 = (m4.value[2] * x + m4.value[6] * y + m4.value[10] * z + m4.value[14]) * w;
        this.set(x1, y1, z1);
        return this;
    }
}