import { check } from "./check";
import { Limit } from "../core/Limit";
import { IMipmap } from "../pool/MipmapPool";
import { ITexInfo } from "../res/GTexture";
import { Extension } from "../core/Extension";
import { isPowerOf2 } from "./isPowerOf2";
import { getPixelSize } from "./getPixelSize";
import { STextureColor, STextureComponent, STextureCompressed } from "../core/Support";
import { CTextureChannelCount, CTextureColor, CTextureCompressed } from "../core/Constant";

/**
 * 
 * @param opts 
 * @param extLib 
 * @param limLib 
 */
const checkTexture2D = (
    opts: {
        texColor?: STextureColor | STextureCompressed,
        inTexColor?: STextureColor | STextureCompressed,
        component?: STextureComponent,
        width?: number,
        height?: number,
        channels?: number,
        compressed?: boolean                                //texColor
    },
    extLib: Extension,
    limLib: Limit
): void => {
    opts.component = opts.component || 'BYTE';
    opts.width = opts.width || 1;
    opts.height = opts.height || 1;
    opts.texColor = opts.texColor || 'RGBA';
    opts.channels = opts.channels || CTextureChannelCount[CTextureColor[opts.texColor]];
    opts.compressed = CTextureCompressed[opts.texColor] ? true : false;
    check(opts.component === 'FLOAT' && extLib.get('OES_texture_float'), `CheckTexture2D error: OES_texture_float`);
    check(opts.component === 'HALF_FLOAT_OES' && extLib.get('OES_texture_float'), `CheckTexture2D error: OES_texture_float`)
    check((opts.component === 'UNSIGNED_SHORT' || opts.component === 'UNSIGNED_INT' || opts.component === 'UNSIGNED_INT_24_8_WEBGL') && extLib.get('WEBGL_depth_texture'), `CheckTexture2D error: WEBGL_depth_texture`)
    check(opts.channels > 0 && opts.channels <= 4, `CheckTexture2D error: `);
    check(opts.width > 0 && opts.width <= limLib.maxTextureSize && opts.height > 0 && opts.height <= limLib.maxTextureSize, `CheckTexture2D error: ${limLib.maxTextureSize}`);
}

/**
 * 
 * @param texInfo 
 * @param mipData 
 * @param extLib 
 * @param limLib 
 */
const checkMipmapTexture2D = (
    texInfo: ITexInfo,
    mipData: IMipmap,
    extLib: Extension,
    limLib: Limit,
): void => {
    //1.
    const w = mipData.width, h = mipData.height, c = mipData.channels;
    check(w > 0 && w <= limLib.maxTextureSize && h > 0 && h <= limLib.maxTextureSize, `CheckTexture2D error: ${limLib.maxTextureSize}`);
    //2.
    if (texInfo.wrapS !== 'CLAMP_TO_EDGE' || texInfo.wrapT !== 'CLAMP_TO_EDGE')
        check(isPowerOf2(w) && isPowerOf2(h), `CheckTexture2D error: CLAMP2`)
    //3.mipmap
    if (mipData.mipmask === 1) {
        if (w !== 1 && h !== 1)
            check(texInfo.minFilter !== 'LINEAR_MIPMAP_LINEAR' && texInfo.minFilter !== 'LINEAR_MIPMAP_NEAREST' && texInfo.minFilter !== 'NEAREST_MIPMAP_LINEAR' && texInfo.minFilter !== 'NEAREST_MIPMAP_NEAREST', `CheckTexture2D error: min filtermimap`);
    }
    else {
        check(isPowerOf2(w) && isPowerOf2(h), `CheckTexture2D error:CLAMP2`);
        check(mipData.mipmask === (w << 1) - 1, `CheckTexture2D error:/mipmask`)
    }
    //4.
    if (mipData.component === 'FLOAT') {
        check(extLib.get('OES_texture_float'), `CheckTexture2D error: FLOATOES_texture_float`);
        check((texInfo.minFilter !== 'NEAREST' || texInfo.magFilter !== 'NEAREST') && extLib.get('OES_texture_float_linear'), `CheckTexture2D error: filter NEARESTOES_texture_float_linear`);
        check(!texInfo.genMipmaps, `CheckTexture2D error: mipmapfloat`);
    }
    //5.
    for (let i = 0; i < 16; ++i) {
        const mipimg = mipData.images[i];
        if (mipimg) {
            const mw = w >> i, mh = h >> i;
            check(mipData.mipmask & (1 << i), `CheckTexture2D error: mipmap`);
            check(mipimg.width === mw && mipimg.height === mh, `CheckTexture2D error: mipmap images shape`);
            check(mipimg.texColor === mipData.texColor && mipimg.inTexColor === mipData.inTexColor && mipimg.component === mipData.component, `CheckTexture2D error: mipmap image`);
            //compressed
            if (mipimg.compressed) {
                //check size for compressed images
            }
            else if (mipimg.data) {
                const rowSize = Math.ceil(getPixelSize(mipimg.component, c) * mw / mipimg.unpackAlignment) * mipimg.unpackAlignment;
                check(mipimg.data.byteLength === rowSize * mh, `CheckTexture2D error: image`);
            }
        }
    }
    //6.
    if (mipData.compressed)
        check(!mipData.genMipmaps, `CheckTexture2D error: mipmap`);
}

/**
 * 
 * @param info 
 * @param mipmap 
 * @param faces 
 * @param limLib 
 */
const checkTextureCube = (
    info: ITexInfo,
    mipmap: IMipmap,
    faces: IMipmap[],
    limLib: Limit
): void => {
    const w = mipmap.width, h = mipmap.height, c = mipmap.channels;
    check(w > 0 && w <= limLib.maxTextureSize && h > 0 && h <= limLib.maxTextureSize, `checkTextureCube error: `);
    check(w === h, `checkTextureCube error: `);
    check(info.wrapS === 'CLAMP_TO_EDGE' && info.wrapT === 'CLAMP_TO_EDGE', `checkTextureCube error: wrapCLAMP_TO_EDGE`);
    //
    faces.forEach(face => {
        check(face.width === w && face.height === h, `checkTextureCube error: ${face}`);
        // check(face.mipmask === 1, `checkTextureCube error: mimap`);
        const mipmaps = face.images;
        for (let k = 0; k < 16; k++) {
            const img = mipmaps[k];
            if (img) {
                const mw = w >> k, mh = h >> k;
                check(face.mipmask & (1 << k), `checkTextureCube error: mipmap`);
                check(img.width === mw && img.height === mh, `checkTextureCube error: mipmap`);
                check(img.component === mipmap.component && img.inTexColor === mipmap.inTexColor && img.texColor === mipmap.texColor, `checkTextureCube error: component/inTexColor/texColor`);
                if (img.compressed) {
                    //
                }
                else if (img.data) {
                    check(img.data.byteLength === mw * mh * Math.max(getPixelSize(img.component, c), img.unpackAlignment), `checkTextureCube error: mipmap`);
                }
                else {
                    //
                }
            }
        }
    });
}


export {
    checkTextureCube,
    checkTexture2D,
    checkMipmapTexture2D
}