
import { GeodeticCoordinate, Globe, TileLayer } from '../src/index';

const map = new Globe({
    width: window.innerWidth - 18,
    height: window.innerHeight - 24,
    zoom: 3,
    canvas: "mapCanvas",
    coordinate: new GeodeticCoordinate(114.2344412, 33.23313241234342, 10000)
});


const layer = new TileLayer();
map.add(layer);

setTimeout(() => {
    map.flyTo(new GeodeticCoordinate(114, 30.5), 10, 1000);
}, 1000);