/**
 * reference https://github.com/toji/gl-matrix/blob/master/src/gl-matrix/quat.js
 * switch to es6 syntax
 * warning:if you don't want to change the source value,please use quat.clone().* instead of quat.*
 * @author yellow 2017/5/10
 */

import { GLMatrix } from "./GLMatrix";
import { Mat3 } from "./Mat3";
import { Mat4 } from "./Mat4";
import { Vec3 } from "./Vec3";


/**
 * @class Quaternion
 * @name quat
 */
export class Quat {
    /**
     * 
     */
    private _out: number[];
    /**
     * 
     */
    private _r1: Vec3;
    /**
     * 
     */
    private _r2: Vec3;
    /**
     * 
     */
    private _r3: Vec3;
    /**
     * 
     */
    private _s1: Quat;
    /**
     * 
     */
    private _s2: Quat;
    /**
     * quat array store
     * Creates a new identity quat
     */
    constructor() {
        this._out = new Array(4);
        this._out[0] = 0;
        this._out[1] = 0;
        this._out[2] = 0;
        this._out[3] = 1;
    }
    /**
     * 
     */
    get x() {
        return this._out[0];
    }
    /**
     * 
     */
    get y() {
        return this._out[1];
    }
    /**
     * 
     */
    get z() {
        return this._out[2];
    }
    /**
     * 
     */
    get w() {
        return this._out[3];
    }
    /**
     * adapter for webgl matrix
     * get the array directly
     * @memberof quat
     * @return {Array}
     */
    get value() {
        return this._out;
    }
    /**
     * generic a quat from mat3
     * @param {mat3} mat the 3x3 matrix 
     */
    static fromMat3(mat: Mat3): Quat {
        // Algorithm in Ken Shoemake's article in 1987 SIGGRAPH course notes
        // article "Quaternion Calculus and Fast Animation".
        const qua = new Quat(),
            fTrace = mat.value[0] + mat.value[4] + mat.value[8];
        let fRoot;
        if (fTrace > 0.0) {
            // |w| > 1/2, may as well choose w > 1/2
            fRoot = Math.sqrt(fTrace + 1.0);  // 2w
            qua._out[3] = 0.5 * fRoot;
            fRoot = 0.5 / fRoot;  // 1/(4w)
            qua._out[0] = (mat._out[5] - mat._out[7]) * fRoot;
            qua._out[1] = (mat._out[6] - mat._out[2]) * fRoot;
            qua._out[2] = (mat._out[1] - mat._out[3]) * fRoot;
        } else {
            // |w| <= 1/2
            let i = 0;
            if (mat.value[4] > mat.value[0]) i = 1;
            if (mat.value[8] > mat.value[i * 3 + i]) i = 2;
            var j = (i + 1) % 3;
            var k = (i + 2) % 3;
            fRoot = Math.sqrt(mat._out[i * 3 + i] - mat._out[j * 3 + j] - mat._out[k * 3 + k] + 1.0);
            qua._out[i] = 0.5 * fRoot;
            fRoot = 0.5 / fRoot;
            qua._out[3] = (mat._out[j * 3 + k] - mat._out[k * 3 + j]) * fRoot;
            qua._out[j] = (mat._out[j * 3 + i] + mat._out[i * 3 + j]) * fRoot;
            qua._out[k] = (mat._out[k * 3 + i] + mat._out[i * 3 + k]) * fRoot;
        }
        return qua;
    }
    /**
     * set the value of quat
     * @param x 
     * @param y 
     * @param z 
     * @param w 
     */
    set(x: number, y: number, z: number, w: number): Quat {
        this._out[0] = x;
        this._out[1] = y;
        this._out[2] = z;
        this._out[3] = w;
        return this;
    }
    /**
     * Creates a new quat initialized with values from an existing quaternion
     */
    clone(): Quat {
        const qua = new Quat();
        qua.set(qua._out[0], qua._out[1], qua._out[2], qua._out[3]);
        return qua;
    }
    /**
     * Set a quat to the identity quaternion
     */
    identity(): Quat {
        this._out[0] = 0;
        this._out[1] = 0;
        this._out[2] = 0;
        this._out[3] = 1;
        return this;
    }
    /**
     * https://github.com/toji/gl-matrix/blob/master/src/quat.js
     * @param v1 the initial vector
     * @param v2 the destination vector
     */
    rotationTo(v1: Vec3, v2: Vec3): Quat {
        this._r1 = this._r1 || new Vec3();
        this._r2 = this._r2 || new Vec3().set(1, 0, 0);
        this._r3 = this._r3 || new Vec3().set(0, 1, 0);
        const dot = v1.dot(v2);
        if (dot < -0.999999) {
            this._r1 = this._r3.clone().cross(v1);
            if (this._r1.len() < 0.000001) this._r1 = this._r3.clone().cross(v1);
            this._r3.normalize();
            this.setAxisAngle(this._r1, Math.PI);
            return this;
        } else if (dot > 0.999999) {
            this._out[0] = 0;
            this._out[1] = 0;
            this._out[2] = 0;
            this._out[3] = 1;
            return this;
        } else {
            this._r1 = v1.clone().cross(v2);
            this._out[0] = this._r1.value[0];
            this._out[1] = this._r1.value[1];
            this._out[2] = this._r1.value[2];
            this._out[3] = 1 + dot;
            return this.normalize();
        }
    }
    /**
     * Sets the specified quaternion with values corresponding to the given
     * axes. Each axis is a vec3 and is expected to be unit length and
     * perpendicular to all other specified axes.
     * @param {Vec3} vv  the vector representing the viewing direction
     * @param {Vec3} vr the vector representing the local "right" direction
     * @param {Vec3} vu    the vector representing the local "up" direction
     */
    setAxes(vv: Vec3, vr: Vec3, vu: Vec3): Quat {
        const mat = new Mat3().set(
            vr.value[0], vu.value[0], -vv.value[0],
            vr.value[1], vu.value[1], -vv.value[1],
            vr.value[2], vu.value[2], -vv.value[2]
        );
        return Quat.fromMat3(mat);
    }
    /**
     * Sets a quat from the given angle and rotation axis,
     * then returns it.
     * @param {Vec3} axis the axis around which to rotate
     * @param {number} rad
     */
    setAxisAngle(axis: Vec3, rad: number): Quat {
        rad = rad * 0.5;
        const s = Math.sin(rad);
        this._out[0] = s * axis.value[0];
        this._out[1] = s * axis.value[1];
        this._out[2] = s * axis.value[2];
        this._out[3] = Math.cos(rad);
        return this;
    }
    /**
     * https://github.com/mrdoob/three.js/blob/280cc9211b736dd728c37a3e355aa51fb1cf2a42/src/math/Quaternion.js#L352
     * assumes direction vectors vFrom and vTo are normalized
     * @param {Vec3} vFrom 
     * @param {Vec3} vTo 
     */
    setFromUnitVectors(vFrom: Vec3, vTo: Vec3): Quat {
        let r = vFrom.clone().dot(vTo) + 1;
        let x, y, z, w;
        if (r < GLMatrix.EPSILON) {
            r = 0;
            if (Math.abs(vFrom.x) > Math.abs(vFrom.z)) {
                x = - vFrom.y;
                y = vFrom.x;
                z = 0;
                w = r;
            } else {
                x = 0;
                y = - vFrom.z;
                z = vFrom.y;
                w = r;
            }
        } else {
            // crossVectors( vFrom, vTo ); // inlined to avoid cyclic dependency on Vector3
            x = vFrom.y * vTo.z - vFrom.z * vTo.y;
            y = vFrom.z * vTo.x - vFrom.x * vTo.z;
            z = vFrom.x * vTo.y - vFrom.y * vTo.x;
            w = r;
        }
        return this.set(x, y, z, w).normalize();
    }
    /**
     * http://www.euclideanspace.com/maths/geometry/rotations/conversions/matrixToQuaternion/index.htm
     */
    setFromRotationMatrix(m: Mat4): Quat {
        const te = m.value,
            m11 = te[0], m12 = te[4], m13 = te[8],
            m21 = te[1], m22 = te[5], m23 = te[9],
            m31 = te[2], m32 = te[6], m33 = te[10],
            trace = m11 + m22 + m33;
        let s, w, x, y, z;
        if (trace > 0) {
            s = 0.5 / Math.sqrt(trace + 1.0);
            w = 0.25 / s;
            x = (m32 - m23) * s;
            y = (m13 - m31) * s;
            z = (m21 - m12) * s;
        } else if (m11 > m22 && m11 > m33) {
            s = 2.0 * Math.sqrt(1.0 + m11 - m22 - m33);
            w = (m32 - m23) / s;
            x = 0.25 * s;
            y = (m12 + m21) / s;
            z = (m13 + m31) / s;
        } else if (m22 > m33) {
            s = 2.0 * Math.sqrt(1.0 + m22 - m11 - m33);
            w = (m13 - m31) / s;
            x = (m12 + m21) / s;
            y = 0.25 * s;
            z = (m23 + m32) / s;
        } else {
            s = 2.0 * Math.sqrt(1.0 + m33 - m11 - m22);
            w = (m21 - m12) / s;
            x = (m13 + m31) / s;
            y = (m23 + m32) / s;
            z = 0.25 * s;
        }
        return this.set(x, y, z, w);
    }
    /**
     * Gets the rotation axis and angle for a given quaternion. 
     * If a quaternion is created with setAxisAngle, 
     * this method will return the same values as providied in the original parameter list OR functionally equivalent values.
     * @example The quaternion formed by axis [0, 0, 1] and angle -90 is the same as the quaternion formed by [0, 0, 1] and 270. 
     *          This method favors the latter.
     * @return [axis,angle]
     */
    getAxisAngle() {
        const rad = Math.acos(this._out[3]) * 2.0,
            s = Math.sin(rad / 2.0),
            axis = new Vec3();
        s === 0.0 ? axis.set(1, 0, 0) : axis.set(this._out[0] / s, this._out[1] / s, this._out[2] / s);
        return [axis, rad];
    }
    /**
     * add two quat's
     * @param {Quat} qua 
     */
    add(qua: Quat): Quat {
        this._out[0] += qua._out[0];
        this._out[1] += qua._out[1];
        this._out[2] += qua._out[2];
        this._out[3] += qua._out[3];
        return this;
    }
    /**
     * Multiplies two quat's
     */
    multiply(qua: Quat): Quat {
        const [ax, ay, az, aw] = this.value,
            [bx, by, bz, bw] = qua.value;
        this._out[0] = ax * bw + aw * bx + ay * bz - az * by;
        this._out[1] = ay * bw + aw * by + az * bx - ax * bz;
        this._out[2] = az * bw + aw * bz + ax * by - ay * bx;
        this._out[3] = aw * bw - ax * bx - ay * by - az * bz;
        return this;
    }
    /**
     * @param {number} s
     */
    scale(s: number): Quat {
        this._out[0] *= s;
        this._out[1] *= s;
        this._out[2] *= s;
        this._out[3] *= s;
        return this;
    }
    /**
     * Rotates a quaternion by the given angle about the X axis
     * @param {number} rad angle (in radians) to rotate
     */
    rotateX(rad: number): Quat {
        rad *= 0.5;
        const [ax, ay, az, aw] = this.value,
            bx = Math.sin(rad),
            bw = Math.cos(rad);
        this._out[0] = ax * bw + aw * bx;
        this._out[1] = ay * bw + az * bx;
        this._out[2] = az * bw - ay * bx;
        this._out[3] = aw * bw - ax * bx;
        return this;
    }
    /**
     * Rotates a quaternion by the given angle about the Y axis
     * @param {number} rad angle (in radians) to rotate
     */
    rotateY(rad: number): Quat {
        rad *= 0.5;
        const [ax, ay, az, aw] = this._out,
            by = Math.sin(rad),
            bw = Math.cos(rad);
        this._out[0] = ax * bw - az * by;
        this._out[1] = ay * bw + aw * by;
        this._out[2] = az * bw + ax * by;
        this._out[3] = aw * bw - ay * by;
        return this;
    }
    /**
     * Rotates a quaternion by the given angle about the Z axis
     * @param {number} rad angle (in radians) to rotate
     */
    rotateZ(rad: number): Quat {
        rad *= 0.5;
        const [ax, ay, az, aw] = this._out,
            bz = Math.sin(rad),
            bw = Math.cos(rad);
        this._out[0] = ax * bw + ay * bz;
        this._out[1] = ay * bw - ax * bz;
        this._out[2] = az * bw + aw * bz;
        this._out[3] = aw * bw - az * bz;
        return this;
    }
    /**
     * Calculates the W component of a quat from the X, Y, and Z components.
     * Assumes that quaternion is 1 unit in length
     * Any existing W component will be ignored.
     */
    calculateW() {
        let [x, y, z, w] = this._out;
        this._out[3] = Math.sqrt(Math.abs(1.0 - x * x - y * y - z * z));
        return this;
    }
    /**
     * Calculates the dot product of two quat's
     * @return {number} dot product of two quat's
     */
    dot(qua: Quat): number {
        const [x0, y0, z0, w0] = this._out,
            [x1, y1, z1, w1] = qua.value;
        return x0 * x1 + y0 * y1 + z0 * z1 + w0 * w1;
    }
    /**
     * Performs a linear interpolation between two quat's
     * @param {Quat} qua the second operand
     * @param {Number} t interpolation amount between the two inputs
     */
    lerp(qua: Quat, t: number): Quat {
        const [ax, ay, az, aw] = this._out;
        this._out[0] = ax + t * (qua.value[0] - ax);
        this._out[1] = ay + t * (qua.value[1] - ay);
        this._out[2] = az + t * (qua.value[2] - az);
        this._out[3] = aw + t * (qua.value[3] - aw);
        return this;
    }
    /**
     * Performs a spherical linear interpolation between two quat
     * benchmarks: http://jsperf.com/quaternion-slerp-implementations
     */
    slerp(a: Quat, b: Quat, t: number): Quat {
        let [ax, ay, az, aw] = a.value,
            [bx, by, bz, bw] = b.value,
            omega, cosom, sinom, scale0, scale1;
        // calc cosine
        cosom = ax * bx + ay * by + az * bz + aw * bw;
        // adjust signs (if necessary)
        if (cosom < 0.0) {
            cosom = -cosom;
            bx = - bx;
            by = - by;
            bz = - bz;
            bw = - bw;
        }
        // calculate coefficients
        if ((1.0 - cosom) > 0.000001) {
            // standard case (slerp)
            omega = Math.acos(cosom);
            sinom = Math.sin(omega);
            scale0 = Math.sin((1.0 - t) * omega) / sinom;
            scale1 = Math.sin(t * omega) / sinom;
        } else {
            // "from" and "to" quaternions are very close 
            //  ... so we can do a linear interpolation
            scale0 = 1.0 - t;
            scale1 = t;
        }
        // calculate final values
        this._out[0] = scale0 * ax + scale1 * bx;
        this._out[1] = scale0 * ay + scale1 * by;
        this._out[2] = scale0 * az + scale1 * bz;
        this._out[3] = scale0 * aw + scale1 * bw;
        return this;
    }
    /**
     * Performs a spherical linear interpolation with two control points
     * @param {Quat} quaI
     * @param {Quat} quaII
     * @param {Quat} quaIII
     * @return
     */
    sqlerp(q1: Quat, q2: Quat, q3: Quat, q4: Quat, t: number): Quat {
        //a.slerp(d,t)  b.slerp(c,t)
        this._s1 =  this._s1 || new Quat();
        this._s2 =  this._s2 || new Quat();
        this._s1 = this._s1.clone().slerp(q1, q4, t);
        this._s2 = this._s2.clone().slerp(q2, q3, t);
        const qua = new Quat().slerp(this._s1, this._s2, 2 * t * (1 - t));
        return qua;
    }
    /**
     * Calculates the inverse of a quat
     * @return {Quat} the inversed quat 
     */
    invert(): Quat {
        const [a0, a1, a2, a3] = this._out,
            dot = a0 * a0 + a1 * a1 + a2 * a2 + a3 * a3,
            invDot = dot ? 1.0 / dot : 0;
        this._out[0] = -a0 * invDot;
        this._out[1] = -a1 * invDot;
        this._out[2] = -a2 * invDot;
        this._out[3] = a3 * invDot;
        return this;
    }
    /**
     * Calculates the conjugate of a quat
     * If the quaternion is normalized, this function is faster than quat.inverse and produces the same result.
     */
    conjugate(): Quat {
        this._out[0] = -this._out[0];
        this._out[1] = -this._out[1];
        this._out[2] = -this._out[2];
        return this;
    }
    /**
     * retrun the length of quat
     * @return {number} 
     */
    len(): number {
        const [x, y, z, w] = this._out;
        return Math.sqrt(x * x + y * y + z * z + w * w);
    }
    /**
     * Normalize a quat
     */
    normalize(): Quat {
        const len = this.len();
        if (len > 0) {
            this._out[0] /= len;
            this._out[0] /= len;
            this._out[0] /= len;
            this._out[0] /= len;
        }
        return this;
    }
    /**
     * Returns a string representation of a quatenion
     * @returns {String} string representation of the vector
     */
    toString(): string {
        return 'quat(' + this._out[0] + ', ' + this._out[1] + ', ' + this._out[2] + ', ' + this._out[3] + ')';
    }
    /**
     * Returns whether or not the quat have approximately the same elements in the same position.
     * @param 
     */
    equals(qua: Quat): boolean {
        const [a0, a1, a2, a3] = this._out,
            [b0, b1, b2, b3] = qua._out;
        return (Math.abs(a0 - b0) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a0), Math.abs(b0)) &&
            Math.abs(a1 - b1) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a1), Math.abs(b1)) &&
            Math.abs(a2 - b2) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a2), Math.abs(b2)) &&
            Math.abs(a3 - b3) <= GLMatrix.EPSILON * Math.max(1.0, Math.abs(a3), Math.abs(b3)));
    }
}